function alterCheckbox(checkBoxId,fakeImageId ){
		
	if ($(checkBoxId).is(':checked')) {
		console.log('done');
		$(fakeImageId).css({
			background: 'url(images/checked.jpg)'
			
		})
	}
	else{
		$(fakeImageId).css({
			background: 'url(images/unchecked.jpg)'
			
		})
	}

}
// 更改checkbox的函数

$('#auto-login-clickable').click(function(){alterCheckbox('#autologin','#auto-login-fake')});
// 登录框checkbox样式

$('#contract-readed').click(function(){alterCheckbox('#agreed','#readed-agreed')});
