// 注册，登录框点击跳转 

function sendToPhone(){
	var num=10;
	var sendTimer=null;
	function sendVerifyCode(){
			num--;
			if(num>=0){
				$('#send-to-phone').html('还有'+num+'秒');
			}
			else{
				$('#send-to-phone').html('重新发送短信');
				$('#send-to-phone').css("background","")
			}
		}
	$('#send-to-phone').click(
		 
		function(){

			
			
		    if($('#send-to-phone').html()==='发送验证码到手机'||$('#send-to-phone').html()==='重新发送短信'){
		    	clearInterval(sendTimer);
				console.log($('#send-to-phone').html());
				$('#send-to-phone').css("background","#888");
				num=10;	
				sendVerifyCode();	
			    sendTimer=window.setInterval(sendVerifyCode,1000);

				
		}
		
	}

	
	)
	}
sendToPhone();
// 验证码倒计时